*{
  box-sizing: border-box;
}

html,body,h1,h2,h3,h4,h5,h6,p,ul,li,a,input{
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  text-decoration: none;
  color: inherit;
}

body{
  background-color: #edeef0;
}

textarea{
  border-radius: 10px;
  border: 1px solid #cbcbcd;
  margin-bottom: 10px;
  outline: none;
  display: flex;
  align-items: center;
  padding: 0 10px;
  margin-right: 5px;
  width: 60%;
}

button{
  outline:none;
  margin: 0;
  cursor: pointer;
  text-align: center;
  background: 0 0;
  background-color: #5181b8;
  border: 0;
  border-radius: 4px;
  padding: 6px 10px;
  color: #fff;
  margin-bottom: 10px;
}